<template>
    <!--begin::Modal-->  
    <div class="modal fade" id="categoryModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <form @submit.prevent="form.id ? update() : store()" class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="formModalLabel" >{{ form.id ? 'تحديث الصنف':'إضافة صنف جديد' }}</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close">
                    </button>
                </div>
                <div class="modal-body row">
                    <div class="col-12 mt-3">
                        <label >الأسم</label>
                        <input v-model="form.name" type="text" autocomplete="off"
                        class="form-control" :class="{ 'is-invalid': form.errors.has('name') }">
                        <has-error :form="form" field="name"></has-error>
                    </div> 
                    <div class="col-12 mt-3">
                        <label :class="`font-${$i18n.locale}`">الصنف التابع له</label>
                        <v-select v-if="parents" dir="rtl" label="name" class="vselect" style="width: 100%;"
                                    :options="parents" :reduce="val => val.id" v-model="form.parent_id"></v-select> 
                        <has-error :form="form" field="name"></has-error>
                    </div> 
                </div> 
                <div class="modal-footer">
                    <button type="button" class="btn btn-light" data-bs-dismiss="modal" >إغلاق</button>
                    <button v-if="form.id" type="submit" class="btn btn-primary" >تحديث</button>
                    <button v-else type="submit" class="btn btn-primary" >حفظ</button>
                </div>
            </form>
        </div>
    </div>
    <!--end::Modal-->
</template>

<script>
import {Form,HasError} from 'vform'
import axios from 'axios';
import "vue-select/dist/vue-select.css";


export default {
    components: {HasError},
    props: ['fetchData'],
    data() {
        return {
            form: new Form({id:null,name:null,parent_id:null}),
            parents: [],
        };
    }, 
    methods: {
        getParents(){
            axios.get('/api/categories',{params: {paginate:0}}).then(({data}) => {
                let that=this
                that.parents=[];
                data.success.categories.forEach(function (category) {
                    let name = that.appendParentName(category);
                    that.parents.push({id:category.id,name:name})
                }); 
            }).catch((error) => {console.log("Error......")})
        },
        create(product_id){
            this.form.reset();
            this.getParents();
            $('#categoryModal').modal('show');
        },
        store(){ 
            this.form.post("/api/categories").then(({data}) => {
                if (data.message) {
                    this.$swal.fire({icon: 'error',title: data.company.name,text: data.message+'!',
                        footer: '<a href>Why do I have this issue?</a>'
                    });
                } else {
                    this.fetchData();
                    $('#categoryModal').modal('hide');
                }
            }).catch((error) => {console.log("Error......")})
        },  
        edit(form){ 
            this.getParents();
            this.form.fill(form);
            $('#categoryModal').modal('show');
        },
        update(){
            this.form.put("/api/categories/"+this.form.id).then((res) => {
                this.fetchData();
                $('#categoryModal').modal('hide');
            }).catch((error) => {console.log("Error......")})
        },  
        destroy(id) {
            // $('#categoryModal').modal('hide');
            this.$swal.fire({
                title:this.$t("modals.general.warning"),text:this.$t("modals.general.r_u_s"),
                icon: 'warning',showCancelButton: true,confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',cancelButtonText: this.$t("modals.general.cancel"),
                confirmButtonText: this.$t("modals.general.yes_delete"),
            }).then((result) => {
                if (result.value) {
                    axios.delete('/api/categories/'+id).then(response => {
                        if(response.data.message){
                            this.$swal.fire({icon: 'error',title: 'Oops...',text: response.data.message+'!',
                                footer: '<a href>Why do I have this issue?</a>'
                            });
                        }else{
                            this.$swal.fire(this.$t("modals.general.deleted"),this.$t("modals.general.delete_success"),'success');
                            this.fetchData();
                        }
                    }).catch(() => {
                        this.$swal.fire({icon: 'error',title: 'Oops...',text: 'Something went wrong!',
                            footer: '<a href>Why do I have this issue?</a>'
                        }); 
                    });
                }
            })
        },
    },
}
</script>

<style scoped >
    .imagePreviewWrapper {
        max-width: 100%;
        border: 1px solid #ced4da;
        border-radius: 20px;
        height: 250px;
        display: block;
        cursor: pointer;
        margin: 0 auto;
    }
    .file-input {
        text-align: center;
    }
    .file-input__input {
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        overflow: hidden;
        position: absolute;
        z-index: -1;
    }
    .file-input__label {
        cursor: pointer;
        display: inline-flex;
        align-items: center;
        border-radius: 4px;
        font-size: 14px;
        font-weight: 600;
        color: #fff;
        background-color: #5b73e8;
        border-color: #5b73e8;
        font-size: 14px;
        padding: 10px 12px;
        box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
    }
    .file-input__label svg {
        height: 16px;
        margin-right: 4px;
    }
    .dz-remove {
            margin-left: 8px;
    }
    .dz-image img {
        width: auto!important;
    }

</style>