<template>
  <layout-default>
    <div class="page-content">
      <div class="container-fluid">

          <!-- start page title -->
          <div class="row">
              <div class="col-12">
                  <div class="page-title-box d-flex align-items-center justify-content-between">
                      <h4 class="mb-0">جدول الأصناف</h4>
                      <div class="page-title-right">
                          <button type="button" @click="$refs.FormModal.create()" class="btn btn-success waves-effect waves-light">
                              + Create New
                          </button>
                          <form-modal ref="FormModal" :fetchData="fetchData"/>
                      </div>

                  </div>
              </div>
          </div>
          <!-- end page title -->
          
          <div class="row">
              <div class="col-12">
                  <div class="card">
                      <div v-if="categories&&categories.data.length" class="card-body">
                          <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                              <thead>
                              <tr>
                                  <th >الأسم</th>
                                  <th >الأصناف</th>
                                  <th >منتجات</th>
                                  <th >خيارات</th>
                              </tr>
                              </thead>
                              <tbody>
                              <tr v-for="(category,index) in categories.data">
                                  <td>{{appendParentName(category)}}</td>
                                  <td>
                                    <p style="float:left;" class="me-2">صنف </p> {{category.categories_count}} 
                                  </td>
                                  <td>
                                    <span class="badge" :class="(category.products_count)?'bg-success':'bg-danger'">
                                      <p style="float:left;" class="m-0 me-2">منتج </p> {{category.products_count}} </span>
                                  </td>
                                  <td>
                                    <div class="btn-group">
                                        <button  type="button" class="btn btn-light dropdown-toggle" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                          More <i class="mdi mdi-chevron-down"></i>
                                        </button>
                                        <div class="dropdown-menu" style="min-width: 120px;">
                                            <!--
                                            <router-link :to="{ name: 'client', params: { client_id: client.id } }" tag="a" class="dropdown-item">
                                                <i class="far fa-eye"></i> Show
                                            </router-link>
                                            -->
                                            <a @click="$refs.FormModal.edit(category)"  class="dropdown-item dropdownItem" href="javascript:;">
                                              <i class="far fa-edit"></i> Edit
                                            </a>
                                            <a @click="$refs.FormModal.destroy(category.id)"  class="dropdown-item dropdownItem" href="javascript:;">
                                              <i class="far fa-trash-alt"></i> Delete
                                            </a>
                                        </div>
                                    </div>
                                  </td>
                              </tr>
                              </tbody>
                          </table>
                          <pagination-nav v-if="categories.last_page>1" :pageSize="5" :currentPage="2" :GoToPage="GoToPage" />
                      </div>
                      <div v-else class="card-body">
                          <beat-loader v-if="loader" :loading="loader" color="#5578eb"></beat-loader>
                          <p v-else class="card-title-desc" style="text-align: center;">
                            {{$t("modals.general.y_d_h",[$t("nav.general.categories")])}}
                          </p>
                      </div>
                  </div>
              </div> <!-- end col -->
          </div> <!-- end row -->
      </div> <!-- container-fluid -->
    </div>
  </layout-default>
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import FormModal from '../../components/modals/category/add';
import axios from 'axios';

export default {
  components: {LayoutDefault,PaginationNav,FormModal},
  data() {
      return {categories:null,loader:false}
    },  
  created() {
    this.fetchData();
  },
  methods: {
    fetchData(num=1) {
      this.categories=null;
      this.loader=true;
      let data = {params: {page:num}};
      axios.get('/api/categories',data).then(({data}) => {
          this.categories=data.success.categories;
          if(!data.success.categories.data.length&&num!=1){
              this.fetchData(1)
          }           
          this.loader=false;
      });  
    },
  },
}
</script>